.expense {
  background-color: #fff;
  border-radius: 5px;
  padding: 15px;
  width: 75%;
  display: flex;
  height: fit-content;
  margin: 15px auto;
}

.detailsContainer {
  display: flex;
  flex-direction: column;
  width: 75%;
}

.date {
  font-size: 35px;
  color: #3f51b5;
  width: 25%;
}

.details {
  display: flex;
}

.expenseType {
  background-color: #3f51b5;
  border-radius: 50px;
  padding: 15px;
  margin-left: 25px;
  width: 200px;
  text-align: center;
}

.expenseType span {
  color: #fff;
  font-weight: bold;
}

.description {
  font-size: 20px;
  color: #201e1f;
}

.priceContainer {
  width: 25%;
  background-color: #f18805;
  border-radius: 5px;
  display: flex;
  height: fit-content;
}

.priceContainer span {
  font-size: 35px;
  color: #fff;
  margin: auto;
}

@media (max-width: 700px) {
  .expense {
    flex-direction: column-reverse;
    width: 100%;
  }

  .priceContainer {
    margin-left: auto;
    padding: 5px;
    width: fit-content;
  }

  .priceContainer span {
    font-size: 20px;
  }

  .detailsContainer {
    width: 100%;
    margin-top: 10px;
  }

  .details {
    width: 100%;
  }

  .date {
    font-size: 15px;
    width: 50%;
    margin: auto;
  }

  .expenseType {
    padding: 5px;
    font-size: 15px;
    margin-left: 0;
  }

  .description {
    font-size: 15px;
    margin-top: 10px;
  }
}
